import AMapLoader from '@amap/amap-jsapi-loader';

export const loadMap = (options = {}) => new Promise((resolve, reject) => {
  const { key, version } = options;
  AMapLoader.load({
    key,
    // version: '2.0',
    version,
    plugins: [], // 'AMap.Scale'
    AMapUI: { //  AMapUI
      version: '1.1', // AMapUI  1.1
      plugins: ['overlay/SimpleMarker'], //  AMapUI ui
    },
    // Loca: { //  Loca 
    //   version: '1.3.2', // Loca  1.3.2
    // },
  }).then((AMap) => {
    resolve(AMap);
  }).catch((e) => {
    reject(e);
  });
});
